import React from 'react';
import './lectures.css';


function Producing() {

  const sectionTitle = () => {
    return(
      <div className="row">
        <div className="col-lg-12 text-center">
            <h2 className="section-heading subpageHeading">Producing</h2>
        </div>
      </div>
    )
  }

  const drawLectures = () => {
    let youtube = <i className="fab fa-youtube"></i>
    
    let producing1 = <div className="singleLectureContainer">
      <div className="singleWebsiteCard">
        <iframe className="websiteImage" src="https://www.youtube.com/embed/Bc7MjNjt0FY" title="  CG          " frameBorder="0"  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
        <div className="projectText">
          <h5>  CG          </h5><br/>
            <p className="bookDescription">
                    ?? D'strict      .       !
            </p>
          <div className="lectureIcons">
            <a href="https://www.youtube.com/watch?v=Bc7MjNjt0FY" target="_blank"  rel="noreferrer">
              {youtube}
            </a>
          </div>
        </div>
      </div>
    </div>

    return (
      <div className="websitesContainer">
        {producing1}
      </div>
    )
  }

  return (
      <section id="producing" className="animated bounceInDown">{sectionTitle()}{drawLectures()}
    </section>
  );
}
  
export default Producing;